body,
html,
#root {
  height: 100%
}

html {
    font-size: max(2.5vmin, 12px);
    color: #fff;
}

h1 { font-size: 6vmin; }
h2 { font-size: 5vmin; }
h3 { font-size: 4vmin; }
h4 { font-size: 3vmin; }
h5 { font-size: 2vmin; }
h6 { font-size: 1vmin; }

.App {
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center; 
    background: linear-gradient(180deg, hsl(0, 0%, 26%), hsl(0, 0%, 6%));
}



